import { Component } from 'react';
import styled, { css } from 'styled-components';
import logoLight from '../assets/logo/logo.png'
import logoDark from '../assets/logo/logo-light.png'
import MenuIcon from '@mui/icons-material/Menu';
import { Link } from 'react-router-dom';
import ButtonBase from '@mui/material/ButtonBase';
// import Grid from '@mui/material/Grid';
// import Box from '@mui/material/Box';

class Navbar extends Component {
  state = {
    expanded: true,
    showHamburgerMenu: false,
    linkList: [
      { to: '/solar/home', text: 'HOME' },
      { to: '/solar/roof', text: 'POWERROOF™' },
      { to: '/solar/pergola', text: 'POWERPERGOLA™' },
      { to: '/solar/get', text: 'GET YOURS' },
    ]
  }

  componentDidMount() {
    this._mounted = true;
  }

  scrollTop = () => {
    setTimeout(() => {
      window.scrollTo({ top: 0, left: 0, behavior: 'smooth' })
    }, 200)
  }

  componentWillUnmount() {
    this._mounted = false;
  }
  _mounted = false
  render() {
    return (
      <Bar
        position={this.props.position}
        expanded={false}
        hide={this.props.hide}
        hamburger={this.state.showHamburgerMenu}
        tabIndex={0}
        onBlur={() => { this.setState({ showHamburgerMenu: false }) }}
      >
        <div style={{ display: 'flex', flexWrap: 'wrap-reverse', justifyContent: 'space-between', height: '100%', width: '100%', maxWidth: '100vw', position: 'relative' }}>
          <LongMenu>
            {this.state.linkList.map(link =>
              <ButtonBase key={link['text']}>
                <NavLink onClick={this.scrollTop} to={link['to']} active={(window.location.pathname === link['to']).toString()}>
                  {link['text'] === "POWERROOF™" ? <span><span style={{ fontWeight: '300' }}>POWER</span><b style={{ fontWeight: '600' }}>ROOF</b><span style={{ fontWeight: '300' }}>™</span></span> :
                    link['text'] === "POWERPERGOLA™" ? <span><span style={{ fontWeight: '300' }}>POWER</span><b style={{ fontWeight: '600' }}>PERGOLA</b><span style={{ fontWeight: '300' }}>™</span></span> :
                      link['text']
                  }
                </NavLink>
              </ButtonBase>
            )}
          </LongMenu>

          <LogoContainer>
            <ButtonBase>
              <Link to='/solar/home' >
                <LogoLight expanded={false} src={logoLight} />
                <LogoDark expanded={false} src={logoDark} />
                <Logo src={logoLight} />
              </Link>
            </ButtonBase>
          </LogoContainer>

          <HamburgerIconWrapper>
            <Button>Sign In</Button>
            <MenuIcon
              style={{
                fontSize: '3rem',
                color: 'white',
                transition: 'all 0.3s ease',
                cursor: 'pointer',
                zIndex: '300',
              }}
              onFocus={e => { e.target.style.color = '#4a69d6' }}
              onClick={() => { this.setState({ showHamburgerMenu: !this.state.showHamburgerMenu }) }}
            />
          </HamburgerIconWrapper>

        </div>

        <HamburgerMenu items={this.state.linkList.length} show={this.state.showHamburgerMenu}>
          {this.state.linkList.map(link =>
            <HamburgerLink onClick={this.scrollTop} key={link['text']} to={link['to']}>{link['text']}</HamburgerLink>
          )}
        </HamburgerMenu>
      </Bar>
    );
  }
}

export default Navbar;



// Custom UI Components

const Bar = styled.nav`    
  top: ${props => (props.hide || false) ? '-4rem' : '0'};
  z-index:200;
  position:${props => props.position || 'fixed'};

  height:3.5rem;
  width:98%;
  max-width: 98vw;
  padding:0 1%;
  
  background: linear-gradient(45deg, #000 0%, #004 100%);
  background-position: 0% -100%;
  background-size: 100% 200%;
  
  transition:all 0.5s ease-in-out;

  &:hover{
    background-position: 0% -200%;
  } 

  ${props => props.hamburger && css`
    background-position: 0% -200%;
  `}

  ${props => !props.expanded && css`
    box-shadow:0 4px 4px 0 rgba(0,0,0,0.6);
    background-position: 0% -100%;
    height:3.5rem;
  `}
  @media only screen and (max-width : 990px) {
    height: 7rem !important;
  }
`;


const LongMenu = styled.div`
  display: flex;
  align-items: center;
  height: 100%;
  z-index: 300;
  @media only screen and (max-width : 990px) {
    /* display: none; */
    width: 96% !important;
    height: 50%;
    padding: 0 2%;
    justify-content: space-between;
    background: rgba(255,255,255,0.1);
  }
`

const LogoContainer = styled.div`
  display:flex;
  justify-content:center;
  align-items:center;
  width:98%;
  padding:0 1%;
  height:100%;
  position:absolute;
  @media only screen and (max-width : 990px) {
    justify-content: flex-start;    
    position: relative;
    width: max-content;
    height: 50%;
    /* margin: 1rem 0; */
  }
`;

const Logo = styled.img`
  height:auto;
  width:9rem;
  opacity: 0;
  cursor: pointer;
  z-index: 0 !important;
`;


const LogoLight = styled.img`
  width:9rem;
  opacity:1;

  position:absolute;
  cursor: pointer;
  transition: all 0.3s ease;
  
  ${Bar}:hover & {
    opacity:0;
  }
`;


const LogoDark = styled.img`
  width:9rem;
  opacity:0;

  position:absolute;
  cursor: pointer;
  transition: all 0.3s ease;
  
  ${Bar}:hover & {
      opacity:1;
  }
`;


const NavLink = styled(Link)`
  margin:0 0.75rem;
  padding:0.5rem 0;
  position:relative;
  outline: none;
  
  color:white;
  font-size: 1rem;
  font-weight: 500;
  text-decoration:none;
  
  transition:all 0.2s ease-in-out;
  cursor:pointer;

  &:after{
    content: '';
    position:absolute;
    left:0;
    bottom:0.25rem;
    height:${props => props.active === 'true' ? '2px' : '0.5px'};
    width:${props => props.active === 'true' ? '100%' : '0'};
    margin-left:${props => props.active === 'true' ? '0' : '50%'};
    transition: all 0.1s ease;
    background-color: ${props => props.active === 'true' ? 'white' : 'orange'};
  }

  &:hover{
    color:orange;
    &:after{
      width:100%;
      margin-left: 0;
      box-shadow:${props => props.active === 'true' ? 'none' : '2px 2px 2px 0px rgba(255,255,255,0.8)'};
    }
  }
`;


const HamburgerMenu = styled.div`
  display: block;
  position: fixed;
  top: 3.5rem;
  right: ${props => props.show ? ('0') : '-17rem'};
  overflow: hidden;
  background-color: rgba(0,0,0,1);
  transition: all 0.3s ease;
  z-index:300;
  width: 15rem;
  opacity: ${props => props.show ? ('1') : '0'};
  height: 100vh !important;
  padding: 2rem 1rem;
`

const HamburgerLink = styled(Link)`
  padding:0 2rem;
  display: flex;
  align-items:center;
  cursor:pointer;
  /* background-color: rgba(0,0,0,0.8); */
  color:white;
  height:3rem;
  text-decoration:none;

  &:focus {
    background-color: rgba(77,77,77,0.3);
  }
`

const HamburgerIconWrapper = styled.div`
  display: flex;
  align-items: center;
  margin: 0 1rem;
  margin-left: auto;
  cursor: pointer;
  z-index: 300;
  /* @media only screen and (max-width : 720px) {
      display: block;
  } */
`

const Button = styled.button`
    color:white;
    background: none;
    border-radius: 0.5rem;
    padding:0.5rem;
    margin:0 2rem;
    border:1px solid white;
    font-size: 1rem;
    width:8rem;
    max-width: 40vw;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease-out;

    &:hover{
        background-color: orange;
        border: 1px solid orange;
        color:black;
    }
`