import React, { Component } from 'react';
import styled, { css } from 'styled-components';
import ArrowUpwardIcon from '@mui/icons-material/ArrowUpward';
import { animateScroll as scroll } from 'react-scroll';

class ScrollToTopIcon extends Component {
    state = { used: false }
    scrollTop = () => {
        scroll.scrollToTop({
            duration: 3000,
            smooth: 'easeInOutCubic'
        });
        this.setState({ used: true });
        setTimeout(() => this.setState({ used: false }), 1500);
    }
    render() {
        var used = this.state.used;
        var scroll = used ? false : this.props.scroll;
        var bottom = this.props.bottom;
        var iconStyle = {
            fontSize: '1.5rem',
            marginRight: '0.5rem',
            marginLeft: '0.75rem'
        };
        return (
            <ToTheTop show={scroll} bottom={bottom} used={used} onClick={this.scrollTop}>
                <ArrowUpwardIcon style={iconStyle} />
                <ScrollText>Back to top</ScrollText>
            </ToTheTop>
        );
    }
}

export default ScrollToTopIcon;

const ScrollText = styled.div`
    transition: all 0.5s ease-in-out;
    font-style: normal;
    font-weight: normal;
    font-size: 1rem;
    color:black;
    white-space:pre;
    margin-right:0.25rem;
    opacity:0;
    font-weight: lighter;
`;

const ToTheTop = styled.div`
    cursor:pointer;
    transition:all 0.3s ease-in-out, right 0.3s ease-out;
    position:fixed;
    display:flex;
    align-items:center;
    bottom:20px;
    right:-100px;
    height:3rem;
    width:3rem;
    background:#F7F7F7;
    border-radius:1.75rem;
    border:1px solid #DDD;
    overflow:hidden;
    opacity:0;
    z-index:500;

    &:hover{
        ${ScrollText} {
            opacity:1;
        }
        width: 8.5rem;
        border: 1px solid #AAA;
    }

    ${props => (props.show) && css`
        right:20px;
        opacity:1;
    `}

    ${props => (props.used) && css`
        opacity:0;
    `}

    ${props => (props.bottom) && css`
        bottom:64px;
        @media (max-width:959px){
            bottom:20px;
        }
    `}
`;