class Footer extends Component {
    render() {
        return (
            <Container ref={this.props.rf}>
                <Grid container justifyContent='space-between' style={{ marginBottom: '8rem' }}>
                    <Grid item xs={12} sm={6} md={4} lg={3} xl={2}>
                        <div style={{ padding: '0 1rem', width: 'max-content', margin: '2rem 2 3rem 3rem' }}>
                            <div style={{ fontSize: '1.5rem' }}>PRODUCTS</div>
                            <FooterLink to='/solar/roof'>Power Roof</FooterLink>
                            <FooterLink to='/solar/home'>Power Pergola</FooterLink>
                            <FooterLink to='/solar/home'>Book Now</FooterLink>
                        </div>
                    </Grid>
                    <Grid item xs={12} sm={6} md={4} lg={3} xl={2}>
                        <div style={{ padding: '0 1rem', width: 'max-content', margin: '2rem 2 3rem 3rem' }}>
                            <div style={{ fontSize: '1.5rem' }}>ARKA ENERGY</div>
                            <FooterLink to='/solar/roof'>Company</FooterLink>
                            <FooterLink to='/solar/home'>Careers</FooterLink>
                            <FooterLink to='/solar/home'>Stories</FooterLink>
                            <FooterLink to='/solar/home'>Locations</FooterLink>
                        </div>
                    </Grid>
                    <Grid item xs={12} sm={6} md={4} lg={3} xl={2}>
                        <div style={{ padding: '0 1rem', width: 'max-content', margin: '2rem 2 3rem 3rem' }}>
                            <div style={{ fontSize: '1.5rem' }}>OTHER</div>
                            <FooterLink to='/solar/roof'>Resources</FooterLink>
                            <FooterLink to='/solar/home'>FAQ</FooterLink>
                            <FooterLink to='/solar/home'>Media Room</FooterLink>
                            <FooterLink to='/solar/home'>Events</FooterLink>
                        </div>
                    </Grid>
                    <Grid item xs={12} sm={6} md={4} lg={3} xl={2}>
                        <div style={{ padding: '0 1rem', width: 'max-content', margin: '2rem 2 3rem 3rem' }}>
                            <div style={{ fontSize: '1.5rem' }}>For Installers</div>
                            <FooterLink to='/solar/roof'>Sign In</FooterLink>
                            <FooterLink to='/solar/home'>Desgin Software</FooterLink>
                        </div>
                    </Grid>
                </Grid>
                <div style={{ position: 'relative', width: '100%', display: 'flex', alignItems: 'center', height: '2rem', justifyContent: 'center', marginTop: 'auto' }}>
                    <Logo src={logo} />
                    <center>© 2021, Arka Energy. All rights reserved.</center>
                    {/* <Socials>
                        <div style={{width:'100%',padding:'1rem 0',textAlign:'center'}}>Follow us on:</div>
                        <Social><FBIcon /></Social>
                        <Social>
                            <IGIcon />
                            <IGColor/>
                        </Social>
                        <Social><TWIcon /></Social>
                        <Social><WAIcon /></Social>
                    </Socials> */}
                </div>
            </Container>
        );
    }
}

export default Footer;

const Container = styled.div`
    padding:7vh 5vw 4vh 5vw;
    background-color:rgba(255,255,255,0.3);
    color:white;
    background-image: linear-gradient(45deg, black, #333);
`;

const Logo = styled.img`
    height:2rem;
    cursor: pointer;
    display: block;
    position:absolute;
    left: 0;
    @media only screen and (max-width : 900px) {
        position:relative;
        margin:auto;
    }
`;

