const FooterLink = styled(Link)`
    color:#666;
    text-decoration:none;
    margin:1rem 0 0.5rem 0;
    display: block;
    position:relative;
    transition:all 0.2s ease;
    width: max-content;
    cursor: pointer;
    font-size: 1rem;
    &:after{
        content: '';
        width:0;
        position:absolute;
        bottom:-0.25rem;
        left:0;
        /* background-color: ${props => props.active === 'true' ? 'white' : '#efdd6f'}; */
        background-color: white;
        height:0.5px;
        transition: all 0.1s ease;
        margin-left:50%;
        /* box-shadow:2px 2px 2px 0px rgba(255,255,255,0.7); */
    }

    &:hover{
        color:white;
        &:after{
            width:100%;
            margin-left: 0;
            box-shadow:${props => props.active === 'true' ? 'none' : '2px 2px 2px 0px rgba(255,255,255,0.8)'};
        }
    }
`